<template>
    <div>
        <el-row>
            <el-col class="search-bar">
                <div>
                    <el-button @click="sortTable">{{orderBy == 1? '正': '倒'}}序排列</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="addItem" icon='plus'>新建罚单</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="exportExc" :loading="exportLoading" icon='document'>导出Excel</el-button>
                </div>
            </el-col>
            <el-col class="search-bar">
                <el-input placeholder="请输入订单号" class="w_200" v-model="searchOrder"></el-input>
                <div class="m_l_20">
                    <el-input placeholder="请输入用户" class="w_200" v-model="searchName"></el-input>
                </div>
                <div class="m_l_20">
                    <el-input placeholder="请输入地址" class="w_200" v-model="searchAddress"></el-input>
                </div>
                <div class="m_l_20">
                    <el-button @click="search" icon="search">搜索</el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table class="table" v-loading="listLoading" :data="tableData" border width="100%" :max-height="tableMaxHeight">
                    <el-table-column align="center" label="序号" width="80">
                        <template scope="scope">
                            {{(scope.$index+1) + pageSize * (currentPage-1)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="orderNumber" label="订单号">
                    </el-table-column>
                    <el-table-column align="center" prop="user.userName" label="用户">
                    </el-table-column>
                    <el-table-column align="center" prop="userOwner.userName" label="车主">
                    </el-table-column>
                    <el-table-column align="center" prop="illegalCode" label="违章信息">
                    </el-table-column>
                    <el-table-column align="center" prop="money" label="罚款">
                    </el-table-column>
                    <el-table-column align="center" prop="points" label="扣分">
                    </el-table-column>
                    <el-table-column align="center" prop="dealway" label="处理方式" :formatter="fomatType">
                    </el-table-column>
                    <el-table-column align="center" prop="" label="已处理凭证上传" width="100">
                        <template scope="scope">
                            <img v-for="(item, index) in scope.row.pictures" :key="index" :src="item" class="viewImg">
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="操作" width="320">
                        <template scope="scope">
                            <el-button type="info" size="small" @click="editItem(scope.$index, scope.row)">查看</el-button>
                            <el-button type="warning" size="small" @click="changeTicketStatus(scope.$index, scope.row)" :disabled="scope.row.ticketStatus == 2">{{scope.row.ticketStatus !== 2? '处　理': '已处理'}}</el-button>
                            <el-button size="small" type="success" @click="uploadDialog = true, itemRow = scope.row" :disabled="scope.row.ticketStatus != 2">上传图片</el-button>
                            <el-button size="small" type="danger" @click="deleteItem(scope.$index, scope.row)" icon='delete2'>删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>

        <el-row>
            <el-col>
                <div class="block pagination">
                    <el-pagination :current-page.sync="currentPage" @current-change="handleCurrentChange" :page-size="pageSize" layout="prev, pager, next, jumper" :page-count="total">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>

        <el-dialog :visible.sync="uploadDialog" @close="resetImgFields('uploadForm')">
            <el-upload
              class="upload-demo"
              :action="API + 'media/file_upload'"
              :on-success="onSuccess"
              :on-error="onError"
              :file-list="viewFileList"
              ref="uploadForm"
              list-type="picture">
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>


            <div slot="footer" class="dialog-footer">
                <el-button @click="uploadDialog = false">取 消</el-button>
                <el-button type="primary" @click="uploadSave()">确 定</el-button>
            </div>
        </el-dialog>
    
        <!-- 查看编辑 -->
        <el-dialog class="edit dialog-info" :visible.sync="editDialog">
            <div class="info-content">
                <el-row>
                    <el-col>
                        用户ID：<span>{{eForm.userId}}</span>
                    </el-col>
                    <el-col>
                        用户：<span>{{eForm.userName}}</span>
                    </el-col>
                    <el-col>
                        手机号：<span>{{eForm.userPhone}}</span>
                    </el-col>
                    <el-col>
                        订单号：<span>{{eForm.orderNumber}}</span>
                    </el-col>
                    <el-col>
                        地点：<span>{{eForm.address}}</span>
                    </el-col>
                    <el-col>
                        违章时间：<span>{{eForm.illegalTime}}</span>
                    </el-col>
                    <el-col>
                        罚款金额：<span>{{eForm.money}}</span>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="10">
                        扣分：<span>{{eForm.points}}</span>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="10">
                        违法说明：<span>{{eForm.illegalCode}}</span>
                    </el-col>
                </el-row>
            </div>
            <!-- <div class="info-radio">
                <el-radio-group v-model="eForm.ticketStatus">
                    <el-radio class="radio" :label="1">正在进行</el-radio>
                    <el-radio class="radio" :label="2">已处理</el-radio>
                </el-radio-group>
            </div> -->
            <p class="info-title">处理信息：</p>
            <img v-for="(item, index) in eForm.pictures" :key="index" :src="item" alt="" style="width:100%;margin-top:10px;">

            <div slot="footer" class="dialog-footer">
                <!-- <el-button @click="editDialog = false">取 消</el-button> -->
                <el-button type="primary" @click="editDialog = false">确 定</el-button>
            </div>
        </el-dialog>

        <!-- 添加 -->
        <el-dialog class="add dialog-info" :visible.sync="addDialog" @close="resetFields('addForm')">
            <div class="info-content">
                <el-form label-position="left" label-width="120px" :model="aForm" :rules="rules" ref="addForm">
                    <el-form-item label="订单号：" prop="orderNumber">
                        <el-input v-model="aForm.orderNumber"></el-input>
                    </el-form-item>
                    <el-form-item label="地点：" prop="address">
                        <el-input v-model="aForm.address"></el-input>
                    </el-form-item>
                    <el-form-item label="时间：" prop="illegalTime">
                        <el-date-picker
                            :editable="false"
                            v-model="aForm.illegalTime"
                            type="datetime"
                            placeholder="选择日期时间"
                            format="yyyy-MM-dd HH:mm:ss"
                            style="width: 100%;">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="罚款金额：" prop="money">
                        <el-input v-model="aForm.money"></el-input>
                    </el-form-item>
                    <el-form-item label="扣分：" prop="points">
                        <el-input v-model="aForm.points"></el-input>
                    </el-form-item>
                    <el-form-item label="违法行为：" prop="illegalCode">
                        <el-input v-model="aForm.illegalCode"></el-input>
                    </el-form-item>
                </el-form>
            </div>

            <div slot="footer" class="dialog-footer">
                <el-button @click="addDialog = false">取 消</el-button>
                <el-button type="primary" @click="addSave('addForm')">确 定</el-button>
            </div>
        </el-dialog>

         <!-- 选择处理方式 -->
        <el-dialog class="dialog-info" title="请选择处理方式" :visible.sync="dealwayDialog">
            <div class="info-radio">
                <el-form>
                    <el-radio-group v-model="dealwayValue">
                        <el-radio class="radio" :label="1">自负处理</el-radio>
                        <el-radio class="radio" :label="2">委托处理</el-radio>
                    </el-radio-group>
                </el-form>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="ticketStatusSave()">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        var positiveNum = (rule, value, callback) => {
          if (!/^([1-9]\d*\.\d*|0\.\d+|[1-9]\d*|0)$/.test(value)) {
            callback(new Error('请输入正确数值'));
          } else {
            callback();
          }
        }
        return {
            // 搜索词
            orderWord: '',
            orderName: '',
            orderAddress: '',
            searchOrder: '',
            searchName: '',
            searchAddress: '',
            orderBy: 2,
            listLoading: false,
            exportLoading: false,
            tableData: [],
            // 编辑-表单
            eForm: {},
            // 添加-表单
            aForm: {},
            //上传文件列表
            dealwayValue: 1,// 处理方式
            viewFileList: [],//上传文件的缓存变量
            fileList: [], //上传文件
            itemRow: '',// 正在编辑的行标

            editDialog: false, //弹出框-编辑
            addDialog: false, //弹出框-新建
            uploadDialog: false, //弹出框-上传图片
            dealwayDialog: false, // 处理方式弹窗
            // 分页
            currentPage: 1,
            pageSize: 20,
            total: 0,
            // 表格距离顶部的高度
            tableTop: 0,
            // 验证规则
            rules: {
                orderNumber: [{required: true, message: '内容不能为空', trigger: 'blur'}],
                address: [{required: true, message: '内容不能为空', trigger: 'blur'}],
                illegalTime:  [{ type: 'date', required: true, message: '请选择日期', trigger: 'change' }],
                money: [{validator: positiveNum, required: true, trigger: 'blur'}],
                points: [{validator: positiveNum, required: true, trigger: 'blur'}],
                illegalCode: [{required: true, message: '内容不能为空', trigger: 'blur'}]
            }
        }
    },
    computed: {
        getIllegalTime() {
            var date = this.aForm.illegalTime
            var str = `${this.$common.getNowTime(date)} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}` 
            return str
        },
        // 计算表格高度
        tableMaxHeight() {
            this.$store.commit('setTableMaxHeight', this.tableTop)
            return this.$store.state.tableMaxHeight
        }
    },
    props: ['API'],
    methods: {
        search(ev) {
            this.orderWord = this.searchOrder
            this.nameWord = this.searchName
            this.addressWord = this.searchAddress
            this.currentPage = 1
            this.getList(this.orderWord, this.nameWord, this.addressWord)
        },
        // 排序
        sortTable() {
            this.orderBy = this.orderBy == 1? 2: 1
            this.getList(this.orderWord, this.nameWord, this.addressWord)
        },
        // 格式化
            // 格式化时间
            formatTime(row, column) {
                return this.$common.getNowTime(row.createAt && row.createAt.time)
            },
            // 格式化状态
            formatStatus(row, column) {
                return row.managerStatus == 1 ? '激活' : row.managerStatus == 2 ? '冻结' : '--';
            },
            // 格式化处理方式
            fomatType(row, column) {
                return row.dealway == 1 ? '自负' : row.dealway == 2 ? '委托' : '未处理';
            },
        // 查看
        editItem(index, row) {
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                   "ticketUuid": row.ticketUuid
                }
            }
            this.$common._post('ticket/detail', json, this, data => {
                this.editDialog = true
                this.eForm= Object.assign({}, data)
                this.eForm.ticketUuid = row.ticketUuid
                this.eForm.pictures = this.eForm.pictures && JSON.parse(this.eForm.pictures) || []
            })
        },
        // 更改状态
        changeTicketStatus(index, row) {
            this.dealwayDialog = true
            this.dealwayValue = 1
            this.itemRow = row
        }, 
        // 保存状态
        ticketStatusSave() {
            this.$confirm('是否确认修改?', '提示', {})
                .then(() => {
                    var json = {
                        "head": this.$common.getUserStorage(),
                        "body": {
                            "ticketUuid": this.itemRow.ticketUuid,
                            "ticketStatus": 2,
                            "dealWay": this.dealwayValue,
                        }
                    }
                    this.$common._post('ticket/updateStatus', json, this, data => {
                        this.dealwayDialog = false
                        this.$message({ type: 'success',message: data.description })
                        this.getList()
                    })
                }).catch(() => {})
        },
        // 添加
        addItem() {
            this.aForm = {
                orderNumber: '',
                address: '',
                illegalTime: '',
                money: '',
                points: '',
                illegalCode: ''
            },
            this.addDialog = true
        },
        addSave(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$confirm('是否确认添加?', '提示', {})
                        .then(() => {
                            var json = {
                                "head": this.$common.getUserStorage(),
                                "body": {
                                    "orderNumber": this.aForm.orderNumber,
                                    "illegalTime": this.getIllegalTime,
                                    "address": this.aForm.address,
                                    "money": this.aForm.money,
                                    "points": this.aForm.points,
                                    "illegalCode": this.aForm.illegalCode
                                }
                            }
                            this.$common._post('ticket/insert', json, this, data => {
                                this.addDialog = false
                                this.$message({
                                    type: 'success',
                                    message: '添加成功!'
                                })
                                this.getList()
                            })
                        })
                        .catch(() => {})
                } else {
                    return false
                }
            })
        },
        // 上传成功
        onSuccess(res, file, fileList) {
            if(res.body.result == 0) {
                this.fileList = Object.assign([], fileList)
            }
            this.$message(res.body.description)
        },
        // 上传失败
        onError(res, file, fileList) {
            this.$message(res.body.description)
        },
        // 确定上传
        uploadSave() {
            if(this.fileList.length<=0) {
                this.$message('图片未上传成功')
                return 
            }
            this.uploadDialog = false
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                    "id": this.itemRow.id,
                    "pictures": this.fileList.map(v => v.response.body.attachment_url)
                }
            }
            this.$common._post('ticket/updatePicture', json, this, data => {
                this.$message({ type: 'success',message: data.description })
                this.getList()
            })
        },
        // 删除
        deleteItem(index, row) {
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                   "ticketUuid": row.ticketUuid
                }
            }
            this.$common.deleteItem(this.API + 'ticket/delete', this, json)
        },
        // 重置表单
        resetFields(formName){
            this.$refs[formName].resetFields()
            this.eForm = {}
        },
        // 重置上传
        resetImgFields(formName) {
            this.$refs[formName].clearFiles()
            this.fileList = []
        },
        exportExc() {
            this.exportLoading = true
            var json = {
                "head": this.$common.getUserStorage(),
                "body": { }
            }
            this.$http.post(this.API + 'exportExcelTicket', json).then(data=>{
                this.exportLoading = false
                var data = data.body
                if (data.body.result == '0') {
                    location.href = data.body.fileUrl
                } else {
                    this.$message(data.body.description)
                }
            })
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList(this.orderWord, this.nameWord, this.addressWord);
        },
        // 获取列表
        getList(order = '', name = '', address = ''){
            this.listLoading = true
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                   "pageIndex": this.currentPage,
                   "pageSize": this.pageSize,
                   "address": address,
                   "userName": name,
                   "orderNumber": order,
                   "orderBy": this.orderBy,
                }
            }
            this.$common._post('ticket/list', json, this, data => {
                this.total = data.total
                this.tableData = data.ticketList
                this.listLoading = false
            }, data=>{
                this.listLoading = false
                this.tableData = []
            })
        }
    },
    mounted(){
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        if (this.$store.state.ticketName) {
            this.searchName = this.$store.state.ticketName
            this.nameWord = this.searchName
        }
        this.getList('', this.nameWord)
        // 表格距离顶部的高度
        this.tableTop = (document.querySelector('#main table').getBoundingClientRect().top)
    }
}
</script>

<style scoped lang="scss">
    .viewImg {
        width: 60px;
        height: 60px;
        border: 1px solid #efefef;
    }
</style>



